@import '../../utils/SCSS/variableStyles';

$borderW: 2px;

*, *:before, *:after {
  @include indents(0, 0);
  box-sizing: border-box;
}

.buttons {
  @include flex(column, center, flex-start);
  max-height: 44px;
  max-width: 90.16px;
  border: none;
  text-align: center;
}

.blob-svg {
  visibility: hidden;
}

.blob {
  $numOfBlobs: 4;
  @include indents(0, 14px 25px);
  @include font(400, 16px, 1, 0);
  text-align: center;
  font-family: $fontFamily;
  color: $whiteColor;
  z-index: 1;
  position: relative;
  background-color: transparent;
  outline: none;
  border: none;
  transition: color 0.5s;
  cursor: pointer;

  &:before {
    content: "";
    z-index: 1;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  &:after {
    content: "";
    z-index: -2;
    position: absolute;
    left: $borderW*1.5;
    top: $borderW*1.5;
    width: 100%;
    height: 100%;
    transition: all 0.3s 0.2s;
  }

  &:hover {
    color: $blackColor;

    &:after {
    transition: all 0.3s;
    left: 0;
    top: 0;
    }
  }

  &__inner {
    z-index: -1;
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $blackColor;
  }

  // additional container created, because in FF blobs are breaking overflow:hidden of element with svg gooey filter
  &__blobs {
    position: relative;
    display: block;
    height: 100%;
    filter: url('#goo');
  }

  &__blob {
    position: absolute;
    top: $borderW;
    width: 100% / $numOfBlobs;
    height: 100%;
    background: $yellowColor;
    box-shadow: none;
    border-radius: 100%;
    transform: translate3d(0,150%,0) scale(1.7);
    transition: transform 0.45s;
    @supports(filter: url('#goo')) {
    transform: translate3d(0,150%,0) scale(1.4);
    }
    @for $i from 1 through $numOfBlobs {
      &:nth-child(#{$i}) {
      left: ($i - 1) * (120% / $numOfBlobs);
      transition-delay: ($i - 1) * 0.08s;
      }
    }

    .blob:hover & {
    transform: translateZ(0) scale(1.7);
      @supports(filter: url('#goo')) {
      transform: translateZ(0) scale(1.4);
      }
    }
  }
}
